<template>
  <el-container >
    <el-header style="text-align: right; font-size: 12px" v-loading="loading">
      <el-row>
        <el-button @click="refresh" >更新</el-button>
      </el-row>
    </el-header>

    <el-main v-for="item of message" :key="item.id">
      <el-card class="box-card">
        <div slot="header" class="messagehead">
          <span class="name">{{item.user_ID}}</span>
          <span class="timestamp">{{item.time}}</span>
        </div>
        <div class="text item">{{item.context}}</div>
      </el-card>
    </el-main>

    <el-footer>
      <el-row>
        <el-button @click="more">更多</el-button>
      </el-row>
    </el-footer>
  </el-container>
</template>

<script>
export default {
  data () {
    return {
      message: [{
        message_ID: '',
        user_ID: '',
        time: '',
        context: ''
      }],
      loading: false,
      start: 0,
      end: 0
    }
  },
  mounted () {
    this.$axios({
      method: 'get',
      url: '/api/message'
    }).then((res) => {
      console.log(res.data)
      // eslint-disable-next-line no-array-constructor
      let i = 0; let mes = []
      while (i < 5) {
        mes[i] = res.data[5 + i]
        i++
      }
      this.start = 5
      this.end = 10
      this.message = mes
    })
  },
  methods: {
    refresh () {
      // eslint-disable-next-line eqeqeq
      if (this.start === 0) {
        this.$message({
          showClose: true,
          message: '没有更新的消息'
        })
      } else {
        this.loading = true
        setTimeout(() => { // 设置延迟执行
          this.loading = false
          this.$axios({
            method: 'get',
            url: '/api/message'
          }).then((res) => {
            // eslint-disable-next-line no-array-constructor
            let j = 0; let mes = []
            console.log(res.data)
            while (j < this.end) {
              mes[j] = res.data[j]
              j++
            }
            this.start = 0
            this.end = 10
            this.message = mes
          })
        }, 500)
      }
    },
    more () {
      // eslint-disable-next-line eqeqeq
      if (this.end === 12) {
        this.$message({
          showClose: true,
          message: '没有更多消息'
        })
      } else {
        this.$axios({
          method: 'get',
          url: '/api/message'
        }).then((res) => {
          let j = this.start; let mes = []
          let k = 0
          while (j < 12) {
            mes[k] = res.data[j]
            j++; k++
          }
          this.end = 12
          this.message = mes
        })
      }
    }
  }
}
</script>

<style>
  .body {
    margin: 0;
  }
  .el-header {
    background-color: #B3C0D1;
    color: #333;
    line-height: 60px;
  }
  .messagehead{
    height:17px;
  }
  .name{
    float: left;
  }
  .timestamp{
    float: right;
  }
</style>



